.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, .5);
  align-items: center;
  justify-content: center;
  padding: 0 19px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s;
  cursor: pointer;
}
